import { WeatherType } from '../api/codes';
const template = document.getElementById('icon-templates');
template.remove();
const elements = [...template.content.children];
const icons = elements.reduce((dict, cur) => {
    const name = cur.dataset.icon;
    dict[name] = cur;
    return dict;
}, {});
export function getCurrentWeatherIcon(type, isDay) {
    const key = type === WeatherType.RAIN
        ? 'rain'
        : type === WeatherType.CLOUDY
            ? 'cloud'
            : isDay
                ? 'sun'
                : 'moon';
    return icons[key].cloneNode(true);
}
export default icons;
